'use strict';

// MODULES //

import hasReplaceSymbolSupport from '@stdlib/assert-has-replace-symbol-support';


// MAIN //

/**
* Replace symbol.
*
* @name ReplaceSymbol
* @constant
* @type {(symbol|null)}
*
* @example
* function replace( str, replacement ) {
*     return replacement;
* }
*
* var obj = {};
* obj[ ReplaceSymbol ] = replace;
*/
var ReplaceSymbol = ( hasReplaceSymbolSupport() ) ? Symbol.replace : null;


// EXPORTS //

export default ReplaceSymbol;
